.MyIcon {
    color: white;
    margin: 0 40px;
    text-shadow: 5px 3px 10px rgb(54, 54, 54);
    font-size: 300%;

    transition: .3s;
}

.MyIcon:hover {
    font-size: 600%;
    color: rgb(31, 177, 97);
    cursor: pointer;
}

@media (max-width: 768px) {
    .MyIcon:hover {
        font-size: 300%;
    }
}

.MyIcon:active {
    color: rgb(82, 255, 93);
    transition: .1s;
}

.MyIcon:last-child:hover{
    color: blueviolet;
}

.MyIcon:last-child:active{
    color: rgb(173, 85, 255);
}

.MyIcon:nth-last-child(2):hover {
    color: blueviolet;
}

.MyIcon:nth-last-child(2):active {
    color: rgb(189, 119, 255);
}