@font-face {
    font-family: 'Neo Tech';
    src: url("../fonts/NeoTech.ttf");
}


::-webkit-scrollbar {width: 0}

.body {
    background: url("../content/background.gif");
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 100%;
    position: absolute;
    margin: 0;
    top: 0;
    left: 0;
}

.preloader {
    display: flex;
    align-items: center;
    position: absolute;
    z-index: 1488;
    background-color: black;
    width: 100%;
    height: 100%;
    

    transition: 1s;
    opacity: 100;
}

.loaded {
    visibility: hidden;
    opacity: 0;
}
.circle {
    margin: 0 auto;
    width: 60px;
    height: 60px;
    border-radius: 100%;
    border: 6px solid transparent;
    border-top: 6px solid #dddddd;

    animation: preloader .5s linear infinite;
}

@keyframes preloader {
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
  }

.App {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    display: flex;

    backdrop-filter: blur(10px);
}

.info {
    margin: 20% auto;
    justify-content: center;
    text-align: center;
    user-select: none;
}

.yasper {
    font-family: "Neo Tech";
    color: white;
    margin: 0px 0px 40px;
    font-size: 100px;
    
    text-shadow: 5px 3px 10px rgb(54, 54, 54);
    transition: .8s;
}

.loop {
    color: rgb(189, 119, 255);
}

.yasper.start {
    font-size: 1px;
}
.yasper.finish {
    font-size: 100px;
    transition: .8s;
}

.mdi.start {
    font-size: 1%;
}
.mdi.tr {
    transition: .8s;
}